	module.exports = {"data":{"markdownRemark":{"html":"<h3>Bank Transfers via CurrencyCloud</h3>\n<p>Hackers can now receive payments through Bank Transfers via CurrencyCloud. This enables them to get paid out in 30 different currencies to almost any country in the world.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2017_2-5a4da134b7614d0ef118b971e7dd8255-c053e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 60.1078167115903%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"july_2017_2\"\n        title=\"\"\n        src=\"/static/july_2017_2-5a4da134b7614d0ef118b971e7dd8255-e7ffa.png\"\n        srcset=\"/static/july_2017_2-5a4da134b7614d0ef118b971e7dd8255-49813.png 125w,\n/static/july_2017_2-5a4da134b7614d0ef118b971e7dd8255-dc8a5.png 250w,\n/static/july_2017_2-5a4da134b7614d0ef118b971e7dd8255-e7ffa.png 500w,\n/static/july_2017_2-5a4da134b7614d0ef118b971e7dd8255-c053e.png 742w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Scope</h3>\n<p>Programs can now define their scope and the list of assets they want hackers to test. This controls what reports can be submitted and helps to prevent noise. Don’t know what a scope is? Learn more <a href=\"/programs/defining-scope.html\">here</a>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2017-346877619ae9c73a1a40912571d5e1bd-203af.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 93.58059914407988%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"july_2017\"\n        title=\"\"\n        src=\"/static/july_2017-346877619ae9c73a1a40912571d5e1bd-e7ffa.png\"\n        srcset=\"/static/july_2017-346877619ae9c73a1a40912571d5e1bd-49813.png 125w,\n/static/july_2017-346877619ae9c73a1a40912571d5e1bd-dc8a5.png 250w,\n/static/july_2017-346877619ae9c73a1a40912571d5e1bd-e7ffa.png 500w,\n/static/july_2017-346877619ae9c73a1a40912571d5e1bd-203af.png 701w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Hacker Reviews</h3>\n<p>Programs now have the ability to review their hackers and to comment on their behavior. Learn more about <a href=\"/programs/hacker-reviews.html\">hacker reviews</a>. </p>\n<h3>Bi-Directional Phabricator Integration</h3>\n<p>We now provide programs with a two-way integration that syncs changes between HackerOne and Phabricator.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2017_3-27fe9b3e364fb910b94958e88d0215a4-7a988.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 36.76122931442081%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"july_2017_3\"\n        title=\"\"\n        src=\"/static/july_2017_3-27fe9b3e364fb910b94958e88d0215a4-e7ffa.png\"\n        srcset=\"/static/july_2017_3-27fe9b3e364fb910b94958e88d0215a4-49813.png 125w,\n/static/july_2017_3-27fe9b3e364fb910b94958e88d0215a4-dc8a5.png 250w,\n/static/july_2017_3-27fe9b3e364fb910b94958e88d0215a4-e7ffa.png 500w,\n/static/july_2017_3-27fe9b3e364fb910b94958e88d0215a4-dda24.png 750w,\n/static/july_2017_3-27fe9b3e364fb910b94958e88d0215a4-7a988.png 846w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Hackathon Inbox Filter</h3>\n<p>During hackathon events, programs can now filter reports in their inbox specific to the hackathon so that these reports can be focused on.</p>\n<h3>Onboarding Changes</h3>\n<p>We’ve updated the words programs encounter when they onboard onto our platform to reflect our new product changes. </p>","frontmatter":{"path":"/changelog/2017/july","title":"July 2017"}}},"pathContext":{}}

